.App{
  display: flex;
  justify-content: center;
  margin-top:33vh ;

}
.drum-pads{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;

}

.drum-pad {
  border: 1px solid black;
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px ;
  margin: 2px;
}

